'use strict';

/**
 * @file     resize
 *          
 * */

import {listener}   from '../listener.js';

/**
 * @memberOf    maple.view
 * @type        {Listener}
 * */
let resize = listener('resize', {
		useDebounce: true
	})
	;

export default resize;


