'use strict';

/**
 * @file    
 *          
 * */

/**
 * @summary     
 * @function    debounce
 * @memberOf    maple.util
 * @param       {Function}  func
 * @param       {Number}    wait
 * @param       {Function}  cancelCB    
 * @return      {Function}
 * */
let debounce = function(func, wait, cancelCB){
	let timeout = null
		, result = function(){
			let that = this || null
				, argv = [].slice.call( arguments )
				;

			if( timeout ){
				clearTimeout( timeout );
			}

			timeout = setTimeout(function(){
				func.apply(that, argv || []);
			}, wait);
		}
		;

	/**
	 * 
	 * */
	result.cancel = function(){
		if( timeout ){
			clearTimeout( timeout );
			timeout = null;

			cancelCB && cancelCB();
		}
	};

	/**
	 * 
	 * */
	result.immediate = function(context=null, argv=[]){
		result.cancel();

		func.apply(context, argv);
	};


	return result;
};

export default debounce;


