'use strict';

import {listener}   from '../listener.js';

/**
 * @class
 * @classdesc   
 * @example
let model = new Model();

//  key-value 
model.setData('index/first', true).then(()=>{
	console.log('');
});
// 
model.setData({
	isLogin: true
	, token: '123123123123123'
	, hybrid: false
}).then(()=>{
	// do something
});

// 
model.getData('index/first').then((value)=>{
	console.log(' ', value);
}, (e)=>{
	console.log('reject  null');
});
//  Json key  value  null
model.getData(['isLogin', 'token', 'hybrid']).then(({isLogin, token, hybrid})=>{
	console.log(isLogin, token, hybrid);    // true, '123123123123123', false
	//  JSON.parse 
});
// 
model.getData('isLogin', 'token', 'hybrid').then(({isLogin, token, hybrid})=>{
	//
})
 * */
class Model{
	/**
	 * @constructor
	 * */
	constructor(){
		this._value = Object.create( null );    //  prototype  hasOwnPropertytoString 
		this._eventList = [];
		this._syncList = [];

		this._listener = listener(this, 'modelChange', (e, topic, value)=>{
			this._sync(topic, value);
		});
	}

	// ----------  ----------
	/**
	 * @summary 
	 * @static
	 * @param   {String}    type
	 * @param   {Model}     model
	 * @desc    
	 * */
	static register(type, model){

		if( type in Model && type in Model._MODEL_CACHE ){
			console.log('type', ' ');
		}
		else{
			Model[type] = model;
		}
	}
	/**
	 * @summary 
	 * @static
	 * @param   {String}            type        
	 * @param   {String|String[]}   aliasName   
	 * */
	static registerAlias(type, aliasName){

		if( !Array.isArray(aliasName) ){
			aliasName = [aliasName];
		}

		aliasName.forEach((d)=>{
			if( !(d in Model._MODEL_ALIAS) ){
				Model._MODEL_ALIAS[d] = type;
			}
			else{
				console.log(d, ' ');
			}
		});
	}
	/**
	 * @summary  type  Model  Model 
	 * @static
	 * @param   {String}            [type]
	 * @param   {Boolean|Object}    [notCache=false]  boolean  false true  object  options  true
	 * @param   {Object}            [options={}]
	 * @return  {Model}              type  Model  Model 
	 * */
	static factory(type, notCache=false, options={}){
		let model
			;

		if( type ){

			if( typeof notCache === 'object' ){
				options = notCache;
				notCache = true;
			}

			//  type 
			if( !(type in Model) && (type in Model._MODEL_ALIAS) ){
				type = Model._MODEL_ALIAS[type];
			}

			// 
			if( type in Model ){

				if( notCache || !(type in Model._MODEL_CACHE) ){    // 

					model = new Model[type]( options );

					if( !notCache ){

						// 
						Model._MODEL_CACHE[type] = model;

						console.log('', type, '', '', type, '');
					}
				}
				else{   // 
					model = Model._MODEL_CACHE[type];

					console.log('', type, '');
				}
			}
			else{
				model = new Model();

				console.log(' ', type, ' ');
			}
		}
		else{
			model = new Model();

			console.log(' model ');
		}

		return model;
	}

	// ----------  ----------
	/**
	 * @summary      null,undefined 
	 * @protected
	 * @param       {*}     value
	 * @return      {String}
	 * */
	_stringify(value){

		if( value === null || value === undefined ){
			value = '';
		}

		return typeof value === 'object' ? JSON.stringify( value ) : value.toString();
	}
	/**
	 * @summary     
	 * @protected
	 * @param       {String}    topic
	 * @param       {*}         value
	 * */
	_trigger(topic, value){
		this._listener.trigger(topic, value);
	}
	/**
	 * @summary     
	 * @protected
	 * @param       {String}    topic
	 * @param       {*}         value
	 * */
	_sync(topic, value){
		if( this._syncList.length ){
			Promise.all( this._syncList.map((d)=>{
				let result
				;

				if( value !== null ){
					result = d.setData(topic, value);
				}
				else{
					result = d.removeData( topic );
				}

				result.catch(function(e){
					console.log(e, d.constructor.name, '');
				});

				return result;
			}) ).then(function(){
				console.log('');
			});
		}
	}

	/**
	 * @summary     
	 * @callback    ModelChangeEvent
	 * @param       {Event}     event
	 * @param       {String}    topic
	 * @param       {*}         newValue
	 * @param       {*}         [oldValue]
	 * @desc         topic,newValue  removeData newValue  null
	 *               Listener  {type: modelChange, target: }
	 * */

	// /**
	//  * @summary      Promise resolve 
	//  * @callback    promiseResolve
	//  * @param       {*}     result
	//  * */
	// /**
	//  * @summary      Promise reject 
	//  * @callback    promiseReject
	//  * @param       {*}     result
	//  * */
	// /**
	//  * @typedef     {Promise.<Object,Error>}    ModelPromise
	//  * @property    {Object}
	//  * @property    {Object}
	//  * @throws      {Error}
	//  * */

	/**
	 * @summary      setData  json 
	 * @protected
	 * @param       {Object}    topic
	 * @return      {Promise}    Promise  resolve  true
	 * */
	_setByObject(topic){
		return Promise.all( Object.keys(topic).map((d)=>{
			return this.setData(d, topic[d]);
		}) ).then((data)=>{
			return !!data;
		});
	}
	/**
	 * @summary      getData 
	 * @protected
	 * @param       {String[]}  topic
	 * @return      {Promise}    Promise  resolve  jsonkey  topic value 
	 * @desc         key  null
	 * */
	_getByArray(topic){
		return Promise.all( topic.map((d)=>{
			return this.getData( d ).catch(()=>{
				return null;
			});
		}) ).then((data)=>{
			return topic.reduce((rs, d, i)=>{
				rs[d] = data[i];

				return rs;
			}, {});
		});
	}
	/**
	 * @summary      removeData 
	 * @protected
	 * @param       {String[]}  topic
	 * @return      {Promise}    Promise  resolve  true
	 * */
	_removeByArray(topic){
		return Promise.all( topic.map((d)=>{
			return this.removeData( d );
		}) ).then((data)=>{
			return !!data;
		});
	}

	// ----------  ----------
	/**
	 * @summary 
	 * @param   {ModelChangeEvent}  callback
	 * */
	on(callback){
		this._listener.add( callback );
	}
	/**
	 * @summary 
	 * @param   {ModelChangeEvent}  callback
	 * */
	off(callback){
		this._listener.off( callback );
	}
	/**
	 * @summary 
	 * @param   {String|Object} topic   
	 * @param   {*}             [value]  nullundefined  topic  object 
	 * @return  {Promise}        Promise  resolve  true
	 * @desc     Object.defineProperty 
				 super.setData _trigger 
	 * */
	setData(topic, value){
		let result
			;

		if( typeof topic === 'object' ){
			result = this._setByObject( topic );
		}
		else{
			if( topic in this._value ){
				this._value[topic] = value;
			}
			else{
				// // todo  object  defineProperty
				// if( typeof value === 'object' ){
				// 	Object.keys( value ).forEach((d)=>{
				// 		this._setObserver(value[d], d, topic);
				// 	});
				// }

				// console.log('', topic, '');

				/**
				 *  (get  set)  writable  value
				 * configurable  true  delete
				 * */
				Object.defineProperty(this._value, topic, {
					enumerable: true
					, configurable: true
					// , value: value
					, set: (newVal)=>{
						console.log('', topic, '', newVal);

						if( newVal !== value ){

							this._trigger(topic, newVal);
						}
					}
					, get(){
						console.log('', topic, '', value, typeof value);
						return value;
					}
				});

				this._trigger(topic, value);
			}

			result = Promise.resolve( true );
		}

		return result
	}
	/**
	 * @summary 
	 * @param   {String|String[]|...String} topic
	 * @return  {Promise}                    Promise  resolve  value reject  null
	 * @desc     topic resolve  jsonkey  topic value 
	 * */
	getData(topic){
		let argc = arguments.length
			, result
			;

		if( Array.isArray(topic) ){
			result = this._getByArray( topic );
		}
		else if( argc > 1 ){
			result = this._getByArray( [].slice.call(arguments) );
		}
		else{
			if( topic in this._value ){
				result = Promise.resolve( this._value[topic] );
			}
			else{
				result = Promise.reject( null );
			}
		}
		
		return result;
	}
	/**
	 * @summary 
	 * @param   {String|String[]}   topic
	 * @return  {Promise}            Promise  resolve  true
	 * @desc     super.removeData _trigger 
	 * */
	removeData(topic){
		let result
			;

		if( Array.isArray(topic) ){
			result = this._removeByArray( topic );
		}
		else{
			try {
				if( topic in this._value ){
					// if( this._value.hasOwnProperty(topic) ){

						delete this._value[topic];

						result = Promise.resolve(true);

						this._trigger(topic, null);
					// }
					// else{
					// 	result = Promise.reject( new Error('') );
					// }
				}
				else{   // model 
					result = Promise.resolve(true);

					this._trigger(topic, null);
				}
			}
			catch(e){
				result = Promise.reject( e );
			}
		}

		return result;
	}
	/**
	 * @summary 
	 * @return  {Promise}    Promise  resolve  true
	 * */
	clearData(){
		this._value = {};

		return Promise.resolve(true);
	}

	/**
	 * @summary  model  model
	 * @param   {Model|Model[]}   model
	 * */
	syncTo(model){
		if( !Array.isArray(model) ){
			model = [model];
		}

		model.forEach((d)=>{
			if( typeof d === 'object' &&
				d instanceof Model &&
				d.constructor !== this.constructor &&
				this._syncList.indexOf( d ) === -1 ){

				this._syncList.push( d );
			}
			else{   // 
				console.log('');
			}
		});
	}
	/**
	 * @summary 
	 * @param   {Model} model
	 * */
	cleanSync(model){
		let i = this._syncList.indexOf( model )
			;

		if( i !== -1 ){
			this._syncList.splice(i, 1);
		}
	}
}

// ----------  ----------
/**
 * 
 * @static
 * */
Model._MODEL_CACHE = {};
/**
 * 
 * @static
 * */
Model._MODEL_ALIAS = {};

export default Model;


