'use strict';

/**
 * @class   Animator
 * */
class Animator {
	/**
	 * @constructor
	 * @param   {Number}            duration            
	 * @param   {Function}          progress            
	 * @param   {String|Function}   [easing='linear']   
	 * */
	constructor(duration, progress, easing='linear'){
		this.duration = duration;
		this.progress = progress;

		this._animate = null;

		// 
		if( typeof easing === 'function' ){
			this.easing = easing;
		}
		else if( typeof easing === 'string' && easing in Animator.timing ){
			this.easing = Animator.timing[easing];
		}
		else{
			this.easing = Animator.timing.linear;
		}
	}

	/**
	 * @param   {Boolean|Function}  [finished]  Boolean Function 
	 * @desc    
	 * */
	run(finished){
		let startTime = Date.now()
			, duration = this.duration
			, self = this
			;

		this._animate = requestAnimationFrame(function step(){
			let p = (Date.now() - startTime) / duration
				, next = true
				;

			if( p < 1.0 ){
				self.progress(self.easing(p), p);
			}
			else{
				if( typeof finished === 'function' ){
					next = finished() === false;
				}
				else{
					next = finished === false;
				}

				if( !next ){
					self.progress(self.easing(1.0), 1.0);
				}
				else{
					startTime += duration;
					self.progress(self.easing(p), p);
				}
			}

			if( next ){
				self._animate = requestAnimationFrame( step );
			}
			else{
				self._animate = null;
			}
		});
	}
	/**
	 * @desc    
	 * */
	stop(){
		if( this._animate ){
			cancelAnimationFrame( this._animate );

			this._animate = null;
		}
	}

	/**
	 * @desc     CSS 
	 * */
	renderCSS(){

	}
}

Animator.timing = {
	linear: function(p){
		return p;
	}
	, easing: function(p){

	}
	, easingIn: function(p){

	}
	, easingInOut: function(p){

	}
	, easingOut: function(p){

	}
};

export default Animator;


