'use strict';

/**
 * @file    
 *          
 * */

/**
 * @summary     
 * @function    throttle
 * @memberOf    maple.util
 * @param       {Function}  func
 * @param       {Number}    wait
 * @param       {Function}  cancelCB    
 * @return      {Function}
 * */
let throttle = function(func, wait, cancelCB){
	let timeout = null
		, result = function(){
			let that = this || null
				, argv = [].slice.call( arguments )
				;

			if( !timeout ){
				func.apply(that, argv || []);

				timeout = setTimeout(function(){
					clearTimeout( timeout );
					timeout = null;
				}, wait);
			}
		}
		;

	/**
	 * 
	 * */
	result.cancel = function(){
		if( timeout ){
			clearTimeout( timeout );
			timeout = null;

			cancelCB && cancelCB();
		}
	};

	/**
	 * 
	 * @param   {Object}    [context=null]   this 
	 * @param   {Array}     [argv=[]]       
	 * */
	result.immediate = function(context=null, argv=[]){
		result.cancel();

		func.apply(context, argv);
		
		timeout = setTimeout(function(){
			clearTimeout( timeout );
			timeout = null;
		}, wait);
	};

	return result;
};

export default throttle;


