'use strict';

/**
 * @file    
 * */

import merge from './util/merge.js';

/**
 * @summary     
 * @function    position
 * @param       {Object}    [options={}]                        
 * @param       {Boolean}   [options.enableHighAccuracy=false]  
 * @param       {Number}    [options.timeout=2000]              
 * @param       {Number}    [options.maximumAge=5000]           
 * @return      {Promise}
 * */
let position = function(options={}){
	let result
		;

	if( 'geolocation' in navigator ){
		result = new Promise(function(resolve, reject){
			let opts = merge(options, position._CONFIG);
			
			navigator.geoposition.getCurrentPosition(resolve, reject, opts);
		});
	}
	else{
		result = Promise.reject( new Error('') );
	}

	return result;
};

position._CONFIG = {
	//  false
	enableHighAccuracy: true
	// 
	, timeout: 2000
	// 
	, maximumAge: 5000
};

/**
 * @exports position
 * */
export default position;


