'use strict';

/**
 * @file    
 *           location  history 
 * */

// const PARAMS_FILTER = [
// 		'10000skip' //  10000skip
// 	]
// 	;

import CONFIG       from '../config.js';
import {listener}   from '../listener.js';

/**
 * @class
 * @classdesc   url 
 * */
class Url{
	/**
	 * @constructor
	 * @param   {String}    [url]
	 * */
	constructor(url){
		let a = document.createElement('a', CONFIG.ceKey)
			;

		a.href = url || location.href;

		this.source     = a.href;
		this.protocol   = a.protocol.replace(':', '');
		this.origin     = a.origin;
		this.host       = a.hostname;
		this.port       = a.port;
		this.path       = a.pathname.replace(/^([^\/])/, '$1');
		// 
		this.relative   = (a.href.match(/tps?:\/\/[^\/]+(.*)/) || ['', ''])[1];
		// 
		this.file       = (a.pathname.match(/\/([^\/?#]+)$/i) || ['', ''])[1];
		// 
		this.dir        = this.path.replace(this.file, '');
		// todo ?
		this.segments   = a.pathname.replace(/^\//, '').split('/');
		// 
		this.params     = this.parseSearch( a.search );
		// hash
		this.hash       = a.hash.slice(1);
		// 
		this.from       = document.referrer;

		a = null;   // 
	}

	// ----------  ----------
	get query(){    // 
		let query = Object.keys( this.params ).reduce((all, d)=>{
				all.push( encodeURIComponent(d) +'='+ encodeURIComponent(this.params[d]) );

				return all;
			}, [])
			;

		return query.length ? '?'+ query.join('&') : '';
	}

	// ----------  ----------
	/**
	 * @summary      url  search 
	 * @param       {String}    [search='']
	 * @return      {Object}
	 * */
	parseSearch(search=''){
		if( search && /^\?/.test(search) ){
			search = search.slice(1);
		}

		return search ? search.split('&').reduce((all, d)=>{
			let temp
				;

			if( d ){
				temp = d.split('=');

				// 
				all[decodeURIComponent(temp[0])] = decodeURIComponent(temp[1] || '').replace(/\?10000skup(=true)?/, '');  // ?10000skip todo ?
			}

			return all;
		}, {}) : {};
	}

	/**
	 * @summary      url
	 * @return      {String}
	 * @todo        onlyPath 
	 * */
	pack(){
		return this.protocol +'://'+
			this.host +
			(!this.port || this.port === '80' ? '' : ':'+ this.port) +
			this.path +
			this.query +
			(this.hash ? '#'+ this.hash : '');
	}
	/**
	 * @summary      params
	 * @param       {Object|String[]|...String} params
	 * @return      {Object}                    this
	 * @desc         string  url  object  url 
	 * */
	changeParams(params){
		let argv
			;

		if( typeof params === 'string' ){
			argv = [].slice.call( arguments );
		}
		else{
			argv = params;
		}

		if( Array.isArray( argv ) ){
			argv.forEach((d)=>{
				if( typeof d === 'string' ){
					delete this.params[d];
				}
			});
		}
		else if( typeof argv === 'object' ){
			Object.keys( argv ).forEach((d)=>{
				this.params[d] = params[d];
			});
		}

		return this;
	}
}

let url = new Url()
	;

/**
 * @summary     Url 
 * @type        {Url}
 * @memberOf    url
 *
 * */
url.Url = Url;
/**
 * @summary      url 
 * @method
 * @memberOf    url
 * @param       {String|Url}    url
 * @return      {Url}            url  Url 
 * */
url.parseUrl = (url)=>{
	if( url instanceof Url ){
		return url;
	}
	else if( typeof url === 'string' ){
		return new Url( url );
	}
	else{
		return new Url();
	}
};
/**
 * @summary      // 
 * @method
 * @memberOf    url
 * @param       {String}    url
 * @return      {String}
 * */
url.addProtocol = (url)=>{
	return /^\/\//.test( url ) ? location.protocol + url : url;
};
/**
 * @summary     
 * @method
 * @memberOf    url
 * */
url.reload = ()=>{
	location.reload();
};
/**
 * @summary     
 * @method
 * @memberOf    url
 * */
url.back = ()=>{
	history.back();
};
/**
 * @summary      url 
 * @method
 * @memberOf    url
 * @param       {Object|String[]|...String} params
 * @param       {*|Object}                  [pushState]
 * @return      {Url}                       this
 * @desc         pushState 
 *              1. params  Object 
 *              2. 1  Object  pushState
 * */
url.push = function(params, pushState){
	let argc = arguments.length
		, argv = arguments
		, state = null
		;

	if( argc > 1 ){
		if( typeof params === 'object' ){
			state = arguments[1];
		}
		else if( typeof arguments[argc -1] === 'object' ){
			state = arguments[argc -1];
			argv = [].slice.call(arguments, 0, -1);
		}
	}

	this.changeParams(...argv);

	//  url  url 
	history.pushState(state, '', this.pack());

	return this;
};
/**
 * @summary      url  url 
 * @method
 * @memberOf    url
 * @param       {String}    href
 * @param       {Object}    [state=null]
 * @return      {Url}       this
 * */
url.pushHistory = function(href, state=null){
	history.pushState(state, '', href);
	
	return this;
};
/**
 * @summary      url 
 * @method
 * @memberOf    url
 * @param       {String}    href
 * */
url.changePage = function(href){
	location.assign( href );
};
/**
 * @summary      url 
 * @method
 * @memberOf    url
 * @param       {Object|String[]|...String} params
 * @return      {Url}                       this
 * */
url.replace = function(params){
	this.changeParams(...arguments);

	//  url 
	history.replaceState(null, '', this.pack());

	return this;
};
/**
 * @summary      url 
 * @method
 * @memberOf    url
 * @param       {String}    href
 * @param       {Object}    [state=null]
 * @return      {Url}       this
 * */
url.replaceHistory = function(href, state=null){
	history.replaceState(state, '', href);

	return this;
};
/**
 * @summary      url 
 * */
url.replacePage = function(href){
	location.replace( href );
};
/**
 * @summary      hashChange 
 * @type        {Listener}
 * @memberOf    url
 * */
url.hashChange = listener('hashchange', (e, newUrl)=>{
	let temp
		;

	if( e.newUrl ){
		temp = url.parseUrl( e.newUrl );
	}
	else if( newUrl ){
		temp = url.parseUrl( newUrl );
	}

	if( temp ){
		url.hash = temp.hash;
	}
});
/**
 * @summary      popstate 
 * @type        {Listener}
 * @memberOf    url
 * */
url.popState = listener('popstate', (e, newUrl)=>{
	let temp
		, state = e.state
		;

	if( newUrl ){
		temp = url.parseUrl( newUrl );
	}
	else{
		temp = url.parseUrl( location.href );
	}

	if( typeof state === 'string' ){
		try{
			state = JSON.parse( state );
		}
		catch(e){}
	}

	// todo state 
	console.log(state);

	//  url  todo 
	Object.keys( temp ).forEach((k)=>{
		url[k] = temp[k];
	});
});

/**
 * @exports     url
 * @type        {Object}
 * @memberOf    maple
 * */
export default url;


