'use strict';

/**
 * @file    view    
 * */

/**
 * @namespace   maple.view
 * */

import resize       from './resize.js';
import scroll       from './scroll.js';
import deviceMotion from './deviceMotion.js';
import message      from './message.js';

// todo ? 
// import pageShow     from './pageShow.js';

/**
 * 
 * */
import beforeunload from './beforeunload.js';
import unload       from './unload.js';

let needRefreshOn = false

	/**
	 * @summary     
	 * @function
	 * @memberOf    maple.view
	 * @desc         Chrome 
	 * */
	, needRefresh = ()=>{
		if( needRefreshOn ){
			return ;
		}

		window.onunload = ()=>{};
		// window.onpagehide = ()=>{
		// 	window.onpageshow = ()=>{
		// 		location.reload();
		// 	}
		// };

		needRefreshOn = true;
	}
	/**
	 * @summary     
	 * @function
	 * @memberOf    maple.view
	 * @param       {Element}       target
	 * @param       {String}        [key]
	 * @return      {Object|Number}
	 * */
	, offset = (target, key)=>{
		let rs = null
			;

		if( 'getBoundingClientRect' in target ){
			rs = target.getBoundingClientRect();

			if( key ){
				rs = rs[key];
			}
		}

		return rs;
	}

	/**
	 * @summary       Web Worker 
	 * @function
	 * @memberOf    maple.view
	 * @param       {Window|Worker} targetWindow
	 * @param       {*}             message
	 * @param       {String}        [targetOrigin='*']  *  url
	 * @param       {*}             [transfer]
	 * */
	, postMessage = (targetWindow, message, targetOrigin='*', transfer)=>{
		targetWindow.postMessage(message, targetOrigin, transfer);
	}
	;

/**
 * @exports view
 * */
export default {
	resize
	, scroll
	, deviceMotion
	, message

	// , pageShow

	, beforeunload
	, unload

	, offset
	, needRefresh
	, postMessage
}


