'use strict';

import Model        from './model.js';
import merge        from '../util/merge.js';
import HandlerQueue from '../util/handlerQueue.js';

/**
 * @file     ajax 
 *           jQuery.ajax 
 * @todo    
 * */

/**
 * @class
 * @classdesc    Model  Model.factory  service s
 * @extends     Model
 *
 * @todo  RESTful API
 * @todo  topic 
 * @todo 
 * */
class ServiceModel extends Model{
	/**
	 * @constructor
	 * @param   {Object}    [config={}]
	 * @param   {String}    [config.baseUrl]
	 // * @param   {Boolean}   [config.isCrossDomain]
	 // * @param   {Boolean}   [config.useLock]
	 * @param   {Number}    [config.timeout]
	 * */
	constructor(config={}){
		super();

		this._config = merge(config, ServiceModel._CONFIG);
		
		this._syncTo = null;

		this.interceptor = {
			req: new HandlerQueue()
			, res: new HandlerQueue()
		};
	}

	// ----------  ----------
	/**
	 * @summary  setData  getData  options 
	 * @param   {Object}    options setData  getData  options 
	 * @return  {Object}
	 * */
	_setOpts(options){
		return merge(options, {
			cache: false
			, dataType: 'json'
			, timeout: this._config.timeout || 10000
			, xhrFields: {
				withCredentials: true
			}
		});
	}

	// ----------  ----------
	/**
	 * @summary      POST 
	 * @override
	 * @param       {String|Object} topic                url url 
	 * @param       {String}        topic.url
	 * @param       {Object}        [options={}]
	 * @param       {Object}        [options.data]
	 * @param       {String}        [options.method]
	 * @return      {Promise}
	 * */
	setData(topic, options={}){
		let result
			;

		if( typeof topic === 'object' ){
			options = topic;
			topic = options.url
		}

		topic = this._config.baseUrl + topic;

		options = this._setOpts( options );

		options.method = options.method || 'POST';

		if( topic ){
			
			// 
			result = this.reqInterceptor(topic, options).then((rs)=>{

				// 
				console.log(' post ', topic);

				return this.request(topic, options);
			}).then((res)=>{

				// 
				return this.resInterceptor( res );
			});
		}
		else{   // topic 
			result = Promise.reject();
		}

		return result;
	}
	/**
	 * @summary      GET 
	 * @override
	 * @param       {String|Object}     topic                url url 
	 * @param       {Object|Boolean}    [options={}]         ajax Boolean  isCache {}
	 * @param       {Object}            [options.data]
	 * @param       {String}            [options.method]
	 * @param       {Boolean}           [isCache=false]      false
	 * @return      {Promise}
	 * @todo     syncTo model 
	 * */
	getData(topic, options={}, isCache=false){
		let result
			;

		if( typeof options === 'boolean' ){
			isCache = options;
			options = {};
		}

		if( typeof topic === 'object' ){
			options = topic;
			topic = options.url;
		}

		if( topic ){
			// 
			if( isCache && this._syncTo ){
				// todo 
				result = this._syncTo.getData( topic );
			}
			else{
				result = Promise.reject();

				topic = this._config.baseUrl + topic;

				options = this._setOpts( options );

				options.method = options.method || 'GET';
			}

			//  reject reject
			result = result.catch(()=>{

				// 
				return this.reqInterceptor(topic, options).then(()=>{

					// 
					console.log(' get ', topic);

					return this.request(topic, options);
				}).then((res)=>{

					// 
					return this.resInterceptor( res );
				}).then((data)=>{   // 
					let result
						;

					if( isCache && this._syncTo ){  // 
						result = this._syncTo.setData(topic, data).then(()=>{
							return data;
						}, (e)=>{
							console.log( e );

							return data;
						});
					}
					else{
						result = data;
					}

					return result;
				});
			});
		}
		else{   // topic 
			result = Promise.reject();
		}

		return result;
	}
	/**
	 * @summary     
	 * @override
	 * @param       {String|Object} topic
	 * @param       {Object}        [options]
	 * @return      {Promise}
	 * @todo         RESTful API delete 
	 * */
	removeData(topic, options){

		return Promise.resolve( true );
	}
	/**
	 * @summary 
	 * */
	clearData(){
		return Promise.resolve( true );
	}
	/**
	 * @summary     
	 * @override
	 * @param       {Model}     model
	 * @todo        
	 * */
	syncTo(model){

		//  model  Model  ServiceModel
		if( (model instanceof Model) && !(model instanceof ServiceModel) ){
			this._syncTo = model;
		}
	}
	/**
	 * @summary 
	 * @param   {String}    topic
	 * @param   {Object}    options
	 * @return  {Promise}
	 * */
	request(topic, options){

		try{
			return $.ajax(topic, options).then((res)=>{ // 
				return res;
			}, ()=>{    // 
				return new Error();
			});
		}
		catch(e){
			console.log(' jquery');
			return Promise.reject();
		}
	}
	/**
	 * @summary 
	 * @param   {String}    topic
	 * @param   {Object}    options
	 * @return  {Promise}
	 * */
	reqInterceptor(topic, options){
		let condition = (rs)=>{
				let result = rs.some((d)=>{
						return d === false;
					})
					;

				if( result ){
					return Promise.reject();
				}
				else{
					return Promise.resolve();
				}
			}
			;

		console.log('', topic);
		return Promise.all( ServiceModel.interceptor.req.fireAll(null, topic, options) ).then( condition ).then(()=>{
			console.log('', topic);

			return Promise.all( this.interceptor.req.fireAll(null, topic, options) ).then( condition );
		});
	}
	/**
	 * @summary 
	 * @param   {Object}    res
	 * @return  {Promise}
	 * */
	resInterceptor(res){
		console.log('');

		return ServiceModel.interceptor.res.fireReduce(null, res).then((res)=>{
			console.log('');
			
			return this.interceptor.res.fireReduce(null, res);
		});
	}
}

/**
 * 
 * @static
 * */
ServiceModel._CONFIG = {
	baseUrl: ''
	// , isCrossDomain: true
	, task: false
	// , jsonp: false
	, timeout: 10000
};

/**
 * 
 * @static
 * */
ServiceModel.interceptor = {
	req: new HandlerQueue()
	, res: new HandlerQueue()
};

/**
 *  ServiceModel
 * */
Model.ServiceModel = ServiceModel;

/**
 *  Model.factory 
 * */
Model.register('service', ServiceModel);
/**
 * 
 * */
Model.registerAlias('service', 's');

export default ServiceModel;


