'use strict';

class HandlerQueue{
	/**
	 * @constructor
	 * */
	constructor(){
		this._queue = [];
		this._currIndex = 0;
	}

	// ----------  ----------
	/**
	 * @summary  handler
	 * @param   {Function}  handler
	 * @return  {Number}    handler  id
	 * */
	add(handler){
		if( typeof handler === 'function' ){
			return this._queue.push( handler );
		}
		else{
			return -1;
		}
	}
	/**
	 * @summary  handler
	 * @param   {Number|Function}   findBy
	 * */
	remove(findBy){

		if( typeof findBy === 'function' ){
			findBy = this._queue.indexOf( findBy );
		}
		else if( typeof findBy === 'number' ){
			findBy = parseInt( findBy );    // 
		}
		else{
			return;
		}

		if( findBy > -1 ){
			this._queue[findBy] = null;
		}
	}
	/**
	 * @summary 
	 * */
	empty(){
		this._queue = [];
		this._currIndex = 0;
	}
	/**
	 * @summary 
	 * @return  {Boolean}
	 * */
	remain(){
		return this._currIndex < this._queue.length;
	}
	/**
	 * @summary  handler
	 * @return  {Function|Null}
	 * @desc     null
	 * */
	next(){
		let result = null
			, i = this._currIndex
			, j = this._queue.length
			;
		
		if( j ){
			for(; i < j; i++ ){
				result = this._queue[i];

				if( result !== null ){
					break;
				}
			}
			if( i !== j ){
				this._currIndex = i +1;
			}
		}

		return result;
	}
	/**
	 * @summary 
	 * */
	reset(){
		this._currIndex = 0;
	}
	/**
	 * @summary  handler handler
	 * @param   {...*}
	 * @return  {*}
	 * @desc     handler 
	 * */
	fire(){
		return this.next()( ...arguments );
	}
	/**
	 * @summary  handler
	 * @param   {Object}    [context]
	 * @param   {...*}
	 * @return  {Array}
	 * @desc     handler  handler 
	 * */
	fireAll(context){
		let args = arguments
			;

		this.reset();

		return this._queue.filter( d=>d !== null ).map( d=>d( ...args ) );
	}
	/**
	 * @summary  reduce  handler handler  handler 
	 * @param   {*}         [init]
	 * @return  {Promise}
	 * @desc     handler 
	 *           reduce  handler 
	 *           reduce  Promise 
	 * */
	fireReduce(init){
		
		this.reset();

		return this._queue.filter( d=>d !== null ).reduce((promise, d)=>{
			return promise.then( rs=>d( rs ) );
		}, Promise.resolve( init ));
	}
}

export default HandlerQueue;


