'use strict';

import Model        from './model.js';
import dateFormat   from '../util/dateFormat.js';

/**
 * @class
 * @classdesc    cookie  Model.factory  cookie c
 * @extends     Model
 * @example
</pre>
let cookieModel = new CookieModel()
	, cookie = Model.factory('cookie')
    , c = Model.factory('c')
	;

console.log( cookie === c );    //  true

cookie.set('memberId', 1, '30d');   //  memberId  cookie  30 
</pre>
 * */
class CookieModel extends Model{
	/**
	 * @constructor
	 * */
	constructor(){
		super();

		if( navigator.cookieEnabled ){
			this._enabled = true;
			this._store = Promise.resolve( true );
		}
		else{
			this._enabled = false;
			this._store = Promise.reject( new Error(' Cookie') );
		}
	}

	// ----------  ----------
	/**
	 * @summary 
	 * @static
	 * @param   {Date|Number|String}    date  Number 
	 * @return  {String}                 UTC 
	 * */
	static _transDate(date){
		if( date instanceof Date ){}
		else{
			date = dateFormat.formatTimeStr( date );

			date = date === 0 ? '' : new Date( Date.now() + date );
		}
		// else if( typeof date === 'number' ){
		// 	temp = new Date();
		// 	temp.setTime( +temp + CookieModel._SHORT_TIME_NUM.d * date );
		// 	date = temp;
		// }
		// else if( temp = CookieModel._SHORT_TIME_EXPR.exec( date ) ){
		// 	date = new Date();
		// 	date.setTime( date.getTime() + Number( temp[1] ) * CookieModel._SHORT_TIME_NUM[temp[2]] );
		// }
		// else{
		// 	date = '';
		// }

		return date && date.toUTCString();
	}

	// ----------  ----------
	/**
	 * @summary      cookie
	 * @protected
	 * @param       {String}                topic
	 * @param       {*}                     value
	 * @param       {Object|Number|String}  [options]           
	 * @param       {String}                [options.path]
	 * @param       {String}                [options.domain]
	 * @param       {Date|Number|String}    [options.expires]
	 * @param       {String}                [options.secure]
	 * @return      {Boolean}                true
	 * @desc         cookie  cookie 
	 * */
	_setCookie(topic, value, options){
		if( typeof options !== 'object' ){
			options = {
				expires: options
			};
		}

		if( options.expires ){
			options.expires = CookieModel._transDate( options.expires );
		}

		document.cookie = encodeURIComponent( topic ) +'='+
			encodeURIComponent( this._stringify(value) ) +
			Object.keys( CookieModel._DEFAULT ).reduce((a, d)=>{    // 

				a += '; '+ d +'=';

				if( d in options ){
					a += options[d];
				}
				else{
					a += CookieModel._DEFAULT[d];
				}

				return a;
			}, '');

		return true;
	}
	/**
	 * @summary      cookie
	 * @protected
	 * @param       {String|String[]}   topic
	 * @return      {Object|String}
	 * */
	_getCookie(topic){
		let cookies = document.cookie
			, i = 0, l
			, value = ''
			, t
			;

		if( cookies ){
			cookies = cookies.split('; ');
		}
		else{
			cookies = [];
		}

		/**
		 * todo 
		 *
		 *  cookie 
		 * 
		 * */
		for(l = cookies.length; i < l; i++ ){
			t = cookies[i].split('=');

			if( topic === decodeURIComponent(t[0]) ){
				value = decodeURIComponent( t[1] );
				break;
			}
		}

		if( value ){
			try{
				value = JSON.parse( value );
			}
			catch(e){}

			// 
			super.setData(topic, value);
		}
		else{
			value = Promise.reject( null );
		}

		return value;
	}
	/**
	 * @summary      setData  json 
	 * @override
	 * @protected
	 * @param       {Object}    topic
	 * @param       {Object}    [options]
	 * @return      {Promise}    Promise  resolve  true
	 * @desc         cookie  _setByObject 
	 * */
	_setByObject(topic, options){
		return Promise.all( Object.keys(topic).map((d)=>{
			return this.setData(d, topic[d], options);
		}) ).then((data)=>{
			return !!data;
		});
	}

	// ----------  ----------
	/**
	 * @summary     
	 * @override
	 * @param       {String|Object}             topic
	 * @param       {*}                         value                topic  object  options
	 * @param       {Object|Number|String}      [options]           
	 * @param       {String}                    [options.path]
	 * @param       {String}                    [options.domain]
	 * @param       {Date|Number|String}        [options.expires]
	 * @param       {String}                    [options.secure]
	 * @return      {Promise}                    Promise  resolve  true
	 * @desc         topic  object value  options
	 * */
	setData(topic, value, options){
		let result
			;

		if( typeof topic === 'object' ){
			options = value;

			result = this._setByObject(topic, options);
		}
		else{
			result = super.setData(topic, value).then(()=>{
				return this._store;
			}).then(()=>{
				return this._setCookie(topic, value, options);
			});
		}
		
		return result;
	}
	/**
	 * @summary 
	 * @param   {String|String[]|...String} topic
	 * @return  {Promise}                    Promise  topic  resolve  value reject  null
	 * @desc     cookie  topic resolve  jsonkey  topic value 
	 * */
	getData(topic){
		let argc = arguments.length
			, result
			;

		if( Array.isArray(topic) ){
			result = this._getByArray( topic );
		}
		else if( argc > 1 ){
			result = this._getByArray( [].slice.call(arguments) );
		}
		else{
			result = super.getData( topic ).catch(()=>{  // 
				return this._store.then(()=>{
					return this._getCookie( topic );
				});
			});
		}

		return result
	}
	/**
	 * @summary  cookie 
	 * @param   {String|String[]|...String} topic
	 * @return  {Object|String}              topic  value null
	 * @desc     cookie  topic  jsonkey  topic value 
	 * */
	getDataSync(topic){
		let argc = arguments.length
			, keyList
			, result
			;

		//  cookie
		if( this._enabled ){

			if( !Array.isArray(topic) ){
				keyList = [topic];
			}
			else if( argc > 1 ){
				keyList = [].slice.call( arguments );
			}
			else{
				keyList = topic;
			}

			result = keyList.reduce((all, d)=>{

				if( d in this._value ){
					all[d] = this._value[d];
				}
				else{
					all[d] = this._getCookie( d ) || null;

					// 
					try{
						all[d] = JSON.parse( all[d] );
					}
					catch(e){}

					super.setData(d, all[d]);
				}

				return all;
			}, {});

			if( !Array.isArray(topic) ){
				result = result[topic];
			}
		}
		else{
			result = null;
		}

		return result;
	}
	/**
	 * @summary 
	 * @param   {String|String[]}   topic
	 * @return  {Promise}            Promise  resolve  true
	 * @desc     _setCookie 
	 * */
	removeData(topic){
		let result
			;
		if( Array.isArray(topic) ){
			result = this._removeByArray( topic );
		}
		else{
			result = super.removeData( topic ).then(()=>{
				return this._setCookie(topic, '', -1);
			});
		}
		return result;
	}
	/**
	 * @summary 
	 * @return  {Promise}    Promise  resolve  true
	 * @desc     clearData  cookie 
	 * */
	clearData(){
		return super.clearData();
	}
	/**
	 * @summary  cookie 
	 * @return  {Number}
	 * */
	getCookieLength(){
		return this._enabled ? document.cookie.length : 0;
	}
}

/**
 *  cookie 
 * @const
 * @static
 * */
CookieModel._DEFAULT = {
	path: '/'
	, domain: ''
	, expires: ''
	, secure: ''
};
// /**
//  * 
//  * @const
//  * @static
//  * */
// CookieModel._SHORT_TIME_EXPR = /^(-?\d+)(s|m|h|d|y)?$/i;
// /**
//  * 
//  * @const
//  * @static
//  * */
// CookieModel._SHORT_TIME_NUM = {
// 	s: 1e3
// 	, m: 6e4
// 	, h: 36e5
// 	, d: 864e5
// 	, y: 31536e6
// };

/**
 *  Model.factory 
 * */
Model.register('cookie', CookieModel);
/**
 * 
 * */
Model.registerAlias('cookie', 'c');

export default CookieModel;


